import clsx from "clsx";
import React from "react";

import blur from "../../../elements/blur.webp";

import _1 from "../../../elements/firstCoins/_1.svg";
import _2 from "../../../elements/firstCoins/_2.svg";
import _3 from "../../../elements/firstCoins/_3.svg";
import _4 from "../../../elements/firstCoins/_4.svg";
import _5 from "../../../elements/firstCoins/_5.svg";
import _6 from "../../../elements/firstCoins/_6.svg";
import _7 from "../../../elements/firstCoins/_7.svg";
import _8 from "../../../elements/firstCoins/_8.svg";
import css from "./style.module.scss";

const iconList = [_1, _2, _3, _4, _5, _6, _7, _8];

export const FirstCoins = () => {
  return (
    <>
      {iconList.map((value, i) => (
        <img
          src={value}
          alt={`coin_${i}`}
          className={clsx(css.icon, css[`icon__${i + 1}`])}
        />
      ))}
      <img src={blur} alt="blur" className={css.blur} />
    </>
  );
};
