import React from "react";

import LightUrl from "../../elements/lights.svg";
import css from "./style.module.scss";
import { Coins } from "../Coins";
import { ImgSet } from "components";

import _1050 from "../../elements/bar/_1050.webp";
import _800 from "../../elements/bar/_800.webp";
import _400 from "../../elements/bar/_400.webp";

export const Banner = () => {
  return (
    <>
      <img src={LightUrl} className={css.lights} />
      <ImgSet
        alt="Bar"
        srcSet={[
          {
            size: 1050,
            src: _1050,
          },
          {
            size: 800,
            src: _800,
          },
          {
            size: 400,
            src: _400,
          },
        ]}
        className={css.bar}
      />

      <Coins />
    </>
  );
};
