import clsx from "clsx";
import React from "react";
import { repeat } from "utils";

import blur from "../../../elements/blur.webp";

import _1 from "../../../elements/secondCoins/_1.svg";
import _2 from "../../../elements/secondCoins/_2.svg";
import _3 from "../../../elements/secondCoins/_3.svg";
import _4 from "../../../elements/secondCoins/_4.svg";
import _5 from "../../../elements/secondCoins/_5.svg";
import _6 from "../../../elements/secondCoins/_6.svg";
import _7 from "../../../elements/secondCoins/_7.svg";
import _8 from "../../../elements/secondCoins/_8.svg";
import css from "./style.module.scss";

const iconList = [_1, _2, _3, _4, _5, _6, _7, _8];

export const SecondCoins = () => {
  return (
    <>
      {iconList.map((value, i) => (
        <img
          src={value}
          alt={`coin_${i}`}
          className={clsx(css.icon, css[`icon__${i + 1}`])}
        />
      ))}

      {repeat(3, (i) => (
        <img
          src={blur}
          alt="blur"
          className={clsx(css.blur, css[`blur__${i + 1}`])}
        />
      ))}
    </>
  );
};
